import React from "react";
import { Col, Row } from "react-bootstrap";
import { IoBody } from "react-icons/io5";

const AboutMe = () => {
  return (
    <div className="mt-3">
      <div class="d-flex align-items-start">
        <IoBody className="fs-1 mt-1" />
        <span className="fs-2 ms-2">About Me</span>
      </div>
      <div className="text-white text-center">
        <Row className="mb-3 mt-2">
          <Col>
            <span className="fs-5">
              <strong>IDE:</strong> Visual Studio Code
            </span>
          </Col>
          <Col>
            <span className="fs-5">
              <strong>Languages:</strong> Polish, English
            </span>
          </Col>
        </Row>
        <Row className="mb-3">
          <span className="fs-5">
            <strong>Technologies:</strong> Python, Django, Django Rest
            Framework, HTML, CSS, JavaScript, NodeJS, React, Redux, Bootstrap,
            MySQL, PostgreSQL, MongoDB
          </span>
        </Row>
      </div>
    </div>
  );
};

export default AboutMe;
