import React from "react";
import { Accordion, Card, useAccordionButton } from "react-bootstrap";

function CustomToggle({ children, eventKey }) {
  const accordionClick = useAccordionButton(eventKey);

  return (
    <button type="button" onClick={accordionClick}>
      {children}
    </button>
  );
}

const TechAccordion = ({ frontend, backend }) => {
  return (
    <Accordion>
      <Card className="border-white border-1 mb-2">
        <Card.Header>
          <CustomToggle eventKey="0">Backend</CustomToggle>
        </Card.Header>
        <Accordion.Collapse eventKey="0">
          <Card.Body>{backend.join(", ")}</Card.Body>
        </Accordion.Collapse>
      </Card>
      <Card className="border-white border-1 mb-2">
        <Card.Header>
          <CustomToggle eventKey="1">Frontend</CustomToggle>
        </Card.Header>
        <Accordion.Collapse eventKey="1">
          <Card.Body>{frontend.join(", ")}</Card.Body>
        </Accordion.Collapse>
      </Card>
    </Accordion>
  );
};

export default TechAccordion;
