import React from "react";
import { Card } from "react-bootstrap";
import TechAccordion from "./TechAccordion";

const ExtendedProjectCard = ({
  title,
  frontend,
  backend,
  desc,
  sourceLink,
}) => {
  return (
    <Card className="h-100 border-white border-1">
      <Card.Body className="d-flex flex-column">
        <Card.Title>{title}</Card.Title>
        <Card.Body>
          <TechAccordion frontend={frontend} backend={backend} />
        </Card.Body>
      </Card.Body>
    </Card>
  );
};

export default ExtendedProjectCard;
